



/**
 *  
 */


const listInitState = {
    isFetching: false,
    data: [
        // {
        //     "id": "5886482f250bf4e2390e9f0d",
        //     "author_id": "5886479c171f3bc843f60185",
        //     "tab": "share",
        //     "content": "<div class=\"markdown-text\"><p>Node.js Foundation</p>\n<p>Node.js</p>\n<blockquote>\n<p>https://identity.linuxfoundation.org/pid/99 </p>\n</blockquote>\n<p><a href=\"https://medium.com/@indexzero/-node-js-foundation-f3ba2dbaa23b#.ur6mjxic6\">https://medium.com/@indexzero/-node-js-foundation-f3ba2dbaa23b#.ur6mjxic6</a></p>\n</div>",
        //     "title": "Node.js Foundation",
        //     "last_reply_at": "2017-02-15T14:35:27.213Z",
        //     "good": false,
        //     "top": true,
        //     "reply_count": 23,
        //     "visit_count": 4453,
        //     "create_at": "2017-01-23T18:15:11.123Z",
        //     "author": {
        //         "loginname": "indexzero",
        //         "avatar_url": "https://avatars.githubusercontent.com/u/4624?v=3&s=120"
        //     }
        // }
    ]
};

const List = (state = listInitState, action) => {

    switch (action.type) {
        case 'index/BEGIN_FETCHING_INDEX':
            return { ...state, isFetching:true };

        case 'index/DONE_FETCHING_INDEX':
            let data = '';
            if(action.page == 1){
                data = action.data;  /* tabpage1data*/
            }else {
                data = [...state.data, ...action.data];
            }

            return { data: data, isFetching: false };

        case 'index/FAIL_FETCHING_INDEX':
            return { ...state, isFetching:false };

        default:
            return state;
    }

};



export default List



