import React from 'react';
import { Link, IndexLink } from 'react-router';

const Nav = ()=> (
        <nav className="index-nav">
            <ul data-flex="box:mean">
                <li>
                    <IndexLink to={{ pathname: '/', query: { tab: undefined } }} activeClassName="on"></IndexLink>
                </li>
                <li>
                    <Link to="/?tab=good" activeClassName="on"></Link>
                </li>
                <li>
                    <Link to="/?tab=share" activeClassName="on"></Link>
                </li>
                <li>
                    <Link to="/?tab=ask" activeClassName="on"></Link>
                </li>
                <li>
                    <Link to="/?tab=job" activeClassName="on"></Link>
                </li>
            </ul>
            <div className="height"></div>
        </nav>
);

export default Nav;

