import React from 'react';

const TopicForm = ({tab, title, content, tabInput, titleInput, contentInput}) => (
    <form className="topic-create">
        <div className="item">
            <select name="tab" defaultValue={tab} onInput={tabInput}>
                <option value=""></option>
                <option value="share"></option>
                <option value="ask"></option>
                <option value="job"></option>
            </select>
        </div>
        <div className="item">
            <input type="text" defaultValue={title} onInput={titleInput} placeholder=" 10 "/>
        </div>
        <div className="item">
            <textarea defaultValue={content} onInput={contentInput} placeholder=" 30 "></textarea>
        </div>
    </form>
);

TopicForm.propTypes = {
    tabInput: React.PropTypes.func.isRequired,      /* */
    titleInput: React.PropTypes.func.isRequired,    /*  */
    contentInput: React.PropTypes.func.isRequired   /*  */
};

export default TopicForm;



