import React from 'react';
import { Router, Route, IndexRoute, hashHistory } from 'react-router';
import Timer from './component/common/Timer';

import IndexList from './component/IndexList';            /*  */
import Topic from './component/Topic';                    /*  */
import TopicCreate from './component/TopicCreate';        /*  */
import Messages from './component/Message/';          /*  */
import UserView from './component/UserView';              /*  */
import SignIn from './component/SignIn';                  /*  */
import SignOut from './component/SignOut';                /*  */

/**
 * 
 * @change 
 * 
 */
const Roots = ({children})=> (
    <div>
        <Timer />
        { children }
    </div>
);


/**
 *  index
 *  import route from './Config/Route';
 *   {route}
 *
 *  index
 *  import Route from './Config/Route';
 *   <Route />
 */
const RouteConfig = (
    <Router history={hashHistory}>
        <Route path="/" component={Roots}>
            <IndexRoute component={IndexList} />
            <Route path="topic/create" component={TopicCreate} />
            <Route path="topic/:id" component={Topic} />
            <Route path="messages" component={Messages} />
            <Route path="user/:loginname" component={UserView} />
            <Route path="signin" component={SignIn} />
            <Route path="signout" component={SignOut} />
        </Route>
    </Router>
);

export default RouteConfig;


